import axios from 'axios'
import qs from 'qs'
import Utils from './Utils.js'


const httpCode = { // http
  400: 'Parameterfehler anfordern', // 
  401: 'Unzureichende Berechtigungen, bitte melden Sie sich erneut an', // , 
  403: 'Der Server verweigert diesen Zugriff', // 
  404: 'Angeforderte Ressource nicht gefunden', // 
  500: 'Interner Serverfehler', // 
  501: 'Der Server untersttzt die in der Anforderung verwendete Methode nicht', // 
  502: 'Gateway-Fehler', // 
  504: 'Gateway-Zeitberschreitung', // 
}

const RqConfig = {
  Debug: process.env.NODE_ENV !== 'production',
  LoadingUtils: { showLoading: Utils.showLoading, hideLoading: Utils.hideLoading, showError: Utils.showError },
}

const instance = axios.create({ timeout: 1000 * 30 })

/**
 * BaseUrl
 * @param baseUrl
 */
export function initial (baseUrl) {
  instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
  instance.defaults.baseURL = baseUrl
}

/**
 * 
 * @param {Object} error 
 */
const errorHandle = (error) => {
  const status = error.response.status
  const tips = status in httpCode ? httpCode[status] : error.response.data.message
  RqConfig.LoadingUtils.showError(tips)
}

const validateResponse = (res) => {
  if (res.data.status) {
    return res.data.status === 'good'
  } else {
    return true
  }
}

instance.interceptors.request.use(
  req => {
    if (req.showLoading) {
      RqConfig.LoadingUtils.showLoading()
    }
    if (req.method === 'get') {
      if (RqConfig.Debug&&!req.noDebug) {
        console.info('------------------------------------------')
        console.info('Sending GET Request:', req.url, req.params)
      }
    }
    if (req.method === 'post' && req.headers.post['Content-Type']
      .includes('application/x-www-form-urlencoded')) {
      if (RqConfig.Debug) {
        console.log('Sending POST Request:', req.url, req.data)
      }
      req.data = qs.stringify(req.data)
    }
    return req
  },
  error => Promise.error(error),
)

instance.interceptors.response.use(
  // 
  res => {


    if (RqConfig.Debug&&!res.config.noDebug) {
      console.log(res.data)
    }
    if (validateResponse(res)) {
      if (res.config.showLoading) {
        RqConfig.LoadingUtils.hideLoading()
      }
      return Promise.resolve(res.data)
    } else {
      if(!res.config.silent){
        if (res.data.info) {
          RqConfig.LoadingUtils.showError(res.data.info)
        } else {
          RqConfig.LoadingUtils.showError()
        }
      }
      return Promise.reject(res)
    }
  },
// 
  error => {
    if (res.config.showLoading) {
      RqConfig.LoadingUtils.hideLoading(false)
    }
    const { response } = error
    if (response) {
      errorHandle(error)
      return Promise.reject(response)
    } else {
      return Promise.reject(error)
    }
  },
)

export { instance as http }
